function KEEP(_,cb){cb();}
"use strict";

function adapter(bridge) {
    return function (request, responseCallBack) {
        bridge.callHandler("onAjaxRequest", request, function (response) {
            responseCallBack(response);
        });
    };
}
//build环境定义全局变量
;
KEEP("!build", function () {
    module.exports = adapter;
});


